<template>
	<view class="content">
		<u-calendar
			:insert="true"
			:show="true"
			:lunar="true" 
			:mode="'month'"
			@change="change"
			@monthSwitch="monthSwitch"
		/>
	</view>
</template>

<script>
	export default {
		data() {
			return {
				adlist:this.myad()[0],
				projectList:{}
			}
		},
		onLoad() {
			this.getprojectList()
		},
		methods: {
			getprojectList(){
				this.request('loadProgramms', 'GET').then(res=>{
					console.log("res:",res)
					if(res){
						let mydata = {}
						 for (var i = 0; i < res.data.length; i++) {
							mydata[i] = {};
							mydata[i]['programImg'] = this.myimgurl() + res.data[i].programImg;
							mydata[i]['id'] =res.data[i].id
							mydata[i]['programName'] = res.data[i].programName
							mydata[i]['programTag'] = res.data[i].programTag
							mydata[i]['programContent'] = res.data[i].programContent
						}
						// console.log(mydata)
						this.projectList = mydata
					}
				})
			},
			change(e) {
				this.currentDate = e.result
				console.log('选择日期:', e.result)
			},
			// 月份切换回调
			monthSwitch(e) {
				console.log('切换月份:', e.year, e.month)
			}
		}
	}
</script>

